import React, { useEffect } from 'react';

import { fade, makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    listStyle: 'none',
    padding: theme.spacing(0.5),
    margin: 0,
    backgroundColor: 'transparent',
    boxShadow: 'none'
  },
  chip: {
    margin: theme.spacing(0.5),
    backgroundColor: fade(theme.palette.common.white, 0.25),
    color: theme.palette.common.white,
  },
}));

export default function SearchChip(props) {
  const classes = useStyles();

  const [chipData, setChipData] = React.useState([]);

  useEffect(() => handleCreateChips(), [props.powerLevel && props.attribute]);

  
  console.log('Power', props.powerLevel);
  console.log('Attr', props.attribute);
  console.log('changed', chipData);

  const handleCreateChips = () => {
    const attribute = props.attribute ? props.attribute : 'All';
    const powerLevel = props.powerLevel ? props.powerLevel : 9;
    const chip = { key: chipData.length, label: attribute, value: powerLevel };
    const newChipData = chipData.map(item => item);
    newChipData[newChipData.length] = chip;
    setChipData(newChipData);
  }

  const handleDelete = (chipToDelete) => () => {
    setChipData((chips) => chips.filter((chip) => chip.key !== chipToDelete.key));
  };

  console.log('Chipdata', chipData)
  return (
    <Paper component='ul' className={classes.root}>
      {chipData.length > 0 
      ? chipData.map((data) => {
        
        return (
          <li key={data.key}>
            <Chip
              label={`${data.label}: ${data.value}`}
              onDelete={handleDelete(data)}
              className={classes.chip}
              size='small'
            />  
          </li>
        )
      })
      : null
    }
    </Paper>
  )
}